.Search {
	-ms-flex-order: 1;
	    order: 1;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	width: 30%;
	height: 100%;
}

.title {
	width: 100%;
	height: 10%;
}

.search-cont {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	width: 100%;
	height: 90%;
	background-color: #F8F6ED;
}

.title h1 {
	margin: 0;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	background-color: #F5C100;
	color: white;
	border: none;
	font-size: 26px;
	width: 100%;
	height: 100%;
}

.filtering-cont {
	display: -ms-flexbox;
	display: flex;
	width: 100%;
	height: 7%;
}

.filtering-cont input {
	height: 100%;
	width: 80%;
	border: none;
	text-align: center;
	border: 1px solid silver;
}

.filtering-cont button {
	width: 20%;
	height: 100%;
	border: none;
	border-radius: 0px;
	background-color: white;
}

.filterButton {
	width: 20%;
	height: 100%;
	background-color: white;
	border: none;
}

.filterButton span {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	width: 100%;
	height: 100%;
	font-size: 22px;
	color: #F5C100;
}

.filterButton ul {
	position: absolute;
	z-index: 1002;
	background-color: white;
	width: 20em;
}

.filterButton button[current='selected'] {
	background-color: #F5C100;
	color: white;
}

.filterButton button {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	text-align: center;
	padding: 0.5em;
	font-weight: bold;
	font-size: 16px;
	width: 100%;
}

.filterButton button:hover span {
	-webkit-animation: searchIcon 1.5s;
	        animation: searchIcon 1.5s;
}

@-webkit-keyframes searchIcon {
	0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
	100% {-webkit-transform: rotate(720deg);transform: rotate(720deg);}
}

@keyframes searchIcon {
	0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
	100% {-webkit-transform: rotate(720deg);transform: rotate(720deg);}
}

.results {
	width: 100%;
	height: 93%;
}

.results ul {
	width: 100%;
	height: 100%;
	overflow-x: hidden;
	overflow-y: scroll;
	list-style-type: none;
	padding: 0;
}

.results button {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	border-bottom: 1px solid #656565;
	border-top: 1px solid #656565;
	height: 4em;
	width: 100%;
	-webkit-transition: 0.3s;
	-o-transition: 0.3s;
	transition: 0.3s;
	background-color: white;
}

.results span {
	padding-left: 2em;
	font-size: 16px;
	font-weight: 600;
	overflow: hidden;
	white-space: nowrap;
	-o-text-overflow: ellipsis;
	   text-overflow: ellipsis;
}

.results button:hover {
	cursor: pointer;
	-webkit-transform: translateX(30px);
	    -ms-transform: translateX(30px);
	        transform: translateX(30px);
	-webkit-transition: 0.3s;
	-o-transition: 0.3s;
	transition: 0.3s;
	background-color: white;
}

@media screen and (max-width: 800px) {
	.filterButton ul {
		width: 10em;
		bottom: -15em;
	}
}

@media screen and (max-width: 700px) {
	.Search {
		position: fixed;
		z-index: 1;
		width: 60%;
		-webkit-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.3);
		        box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.3);
	}

	.title h1 {
		font-size: 1.2em;
	}
}


